.rtg-dialog-content{padding:24px;background:#fff;height:100%;display:flex;flex-direction:column}.rtg-dialog-content>*{flex:0 1 auto}.rtg-dialog-content>.rtg-grow{flex-grow:1}.rtg-thumbnails-list li[data-id]{display:flex;align-items:center;padding:5px 10px 5px 3px}.rtg-thumbnails-list li[data-id] sup{color:#b9b9b9;min-width:20px;margin-right:5px}.rtg-thumbnails-list li[data-id] img{width:auto;height:30px;margin-right:10px}.rtg-thumbnails-list li[data-id]:nth-child(even){background:#fbfbfb}.rtg-thumbnails-list li[data-id]:after{clear:both;content:"";display:block}.rtg-thumbnails-list li[data-id]:hover{background:#1890ff;color:#fff;cursor:pointer}.rtg-thumbnails-list li[data-id]:hover sup,.rtg-thumbnails-list li[data-id]:hover ul{opacity:0}.rtg-highlight-placeholders span{background:#e6e6e6}ul.rtg-analyse-result{margin-left:auto;border:1px solid #e8e8e8;border-radius:5px;overflow:hidden;background:#fff}ul.rtg-analyse-result>li{display:inline-block;padding:5px;border-right:1px solid #f8f8f8;min-width:50px;text-align:center;margin:0}ul.rtg-analyse-result>li:last-of-type{border-right:0}ul.rtg-analyse-result>.rtg-legend{background:#1890ff;color:#fff;min-width:initial;padding:5px 10px}.rtg-dialog-single-preview{height:36px;width:auto;float:right;margin-top:2px}.rtg-single h3{margin-top:0;font-weight:700;border-bottom:1px solid #f0f2f5;padding-bottom:23px}.rtg-single .rtg-size-img{float:left;padding-right:20px;text-align:center}.rtg-single .rtg-size-img div{padding-bottom:10px;font-size:13px;letter-spacing:3px}.rtg-single .rtg-size-img img{margin-bottom:10px}.rtg-single .rtg-container{max-width:550px}.rtg-single .rtg-container code{padding:2px 10px;border-radius:99px;font-weight:400}.rtg-single .rtg-container>div{padding-bottom:6px}.rtg-single .rtg-container>div:first-of-type{padding-bottom:5px;border-bottom:1px solid #ededed;margin-bottom:3px}.rtg-single .rtg-container>div.unused{opacity:.7}.rtg-single .rtg-container>div a{text-decoration:none}.rtg-single .rtg-container>div a:hover{text-decoration:underline}.rtg-single .rtg-container>div div:not(.clear){float:right}.rtg-attachment-compat .alignleft{padding-top:6px}tr.rtg-update-notice th{padding:0}tr.rtg-update-notice .notice{margin:0 20px 10px 26px;border-top:1px solid #ffb900;box-shadow:4px 4px 4px #ffb9001a,1px 1px 0 #ffb900}tr.rtg-update-notice .notice p{margin:0}